import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { catchError, filter, map, mergeMap, take, tap } from 'rxjs/operators';
import { AuthService } from '../../../../shared/firebase/auth.service';
import { combineLatest, from, of } from 'rxjs';
import { environment } from '../../../../../environments/environment';
import { checkPasswordStrength, PasswordStrength } from '../helper/password-strength.function';

@Component({
	selector: 'app-user-actions',
	templateUrl: './user-actions.component.html',
	styleUrls: ['./user-actions.component.scss'],
})
export class UserActionsComponent {

	readonly USER_ACTIONS = {
		VERIFY_EMAIL: 'verifyEmail',
		RESET_PASSWORD: 'resetPassword',
		RECOVER_EMAIL: 'recoverEmail',
	};

	passwordStrength = PasswordStrength;

	password: string;
	isPasswordVisible = true;
	loading = true;
	passwordChanged = false;
	homepage = environment.app_dynamic_link;

	mode$ = this.activeRoute.queryParams.pipe(map(queryParams => queryParams.mode || null));
	oobCode$ = this.activeRoute.queryParams.pipe(map(queryParams => queryParams.oobCode || null));
	verifyEmail$ = this.mode$.pipe(
		filter(mode => mode === this.USER_ACTIONS.VERIFY_EMAIL),
		take(1),
		mergeMap(() => {
			return combineLatest([
				this.oobCode$,
				this.activeRoute.queryParams.pipe(map(queryParams => queryParams.token || null)),
			]);
		}),
		mergeMap(([actionCode, token]) => {
			return from(this.authService.verifyEmailVerificationCode(actionCode, token));
		}),
		catchError(() => {
			return of(false);
		}),
		tap(() => {
			this.loading = false;
		}),
	);

	verifyResetPasswordActionCode$ = this.mode$.pipe(
		filter(mode => mode === this.USER_ACTIONS.RESET_PASSWORD),
		take(1),
		mergeMap(() => this.oobCode$),
		mergeMap((actionCode) => {
			return from(this.authService.verifyPasswordResetCode(actionCode));
		}),
		catchError(() => {
			return of(false);
		}),
		tap(() => {
			this.loading = false;
		}),
	);

	constructor(
		private readonly activeRoute: ActivatedRoute,
		private readonly authService: AuthService,
	) {
	}

	async verifyPassword(password: string): Promise<void> {
		const actionCode = await this.oobCode$.pipe(take(1)).toPromise();
		await this.authService.confirmResetPassword(actionCode, password);
		this.passwordChanged = true;
	}

	getPasswordStrength(): PasswordStrength {
		return checkPasswordStrength(this.password);
	}

}
